@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.btn{width:100%;height:3rem;background-color:#bf9e3b;box-shadow:5px 2px 15px 5px #8c693e;border:none;color:#734f30;font-size:1.25rem;font-weight:bold}

#pad{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr);width:50%;grid-gap:1rem;gap:1rem}

#display{display:flex;flex-direction:column;justify-content:center;align-items:center}

#drum-machine{display:flex;flex-direction:row;justify-content:center;align-items:center;grid-column-gap:2rem;column-gap:2rem;width:80%;height:50vh;background-color:#a6988d;margin:auto;margin-top:10vh;border:2px solid #734f30;border-radius:15px}

body{background-color:#d9c9ba;font-family:"Poppins", sans-serif}

